, 20%);\n}\n\n// TODO: Should remove icon style compatible in v5\n\n// default theme\n.@{menu-prefix-cls} {\n  .reset-component();\n\n  margin-bottom: 0;\n  padding-left: 0; // Override default ul/ol\n  color: @menu-item-color;\n  font-size: @menu-item-font-size;\n  line-height: 0; // Fix display inline-block gap\n  text-align: left;\n  list-style: none;\n  background: @menu-bg;\n  outline: none;\n  box-shadow: @box-shadow-base;\n  transition: background @animation-duration-slow,\n    width @animation-duration-slow cubic-bezier(0.2, 0, 0, 1) 0s;\n  .clearfix();\n\n  &&-root:focus-visible {\n    .accessibility-focus();\n  }\n\n  ul,\n  ol {\n    margin: 0;\n    padding: 0;\n    list-style: none;\n  }\n\n  // Overflow ellipsis\n  &-overflow {\n    display: flex;\n\n    &-item {\n      flex: none;\n    }\n  }\n\n  &-hidden,\n  &-submenu-hidden {\n    display: none;\n  }\n\n  &-item-group-title {\n    height: @menu-item-group-height;\n    padding: 8px 16px;\n    color: @menu-item-group-title-color;\n    font-size: @menu-item-group-title-font-size;\n    line-height: @menu-item-group-height;\n    transition: all @animation-duration-slow;\n  }\n\n  &-horizontal &-submenu {\n    transition: border-color @animation-duration-slow @ease-in-out,\n      background @animation-duration-slow @ease-in-out;\n  }\n  &-submenu,\n  &-submenu-inline {\n    transition: border-color @animation-duration-slow @ease-in-out,\n      background @animation-duration-slow @ease-in-out,\n      padding @menu-animation-duration-normal @ease-in-out;\n  }\n\n  &-submenu-selected {\n    color: @menu-highlight-color;\n  }\n\n  &-item:active,\n  &-submenu-title:active {\n    background: @menu-item-active-bg;\n  }\n\n  &-submenu &-sub {\n    cursor: initial;\n    transition: background @animation-duration-slow @ease-in-out,\n      padding @animation-duration-slow @ease-in-out;\n  }\n\n  &-item a {\n    color: @menu-item-color;\n    &:hover {\n      color: @menu-highlight-color;\n    }\n    &::before {\n      position: absolute;\n      top: 0;\n      right: 0;\n      bottom: 0;\n      left: 0;\n      background-color: transparent;\n      content: '';\n    }\n  }\n\n  // https://github.com/ant-design/ant-design/issues/19809\n  &-item > .@{ant-prefix}-badge a {\n    color: @menu-item-color;\n    &:hover {\n      color: @menu-highlight-color;\n    }\n  }\n\n  &-item-divider {\n    height: 1px;\n    overflow: hidden;\n    line-height: 0;\n    background-color: @border-color-split;\n  }\n\n  &-horizontal &-item,\n  &-horizontal &-submenu {\n    margin-top: -1px;\n  }\n\n  &-horizontal > &-item:hover,\n  &-horizontal > &-item-active,\n  &-horizontal > &-submenu &-submenu-title:hover {\n    background-color: transparent;\n  }\n\n  &-item-selected {\n    color: @menu-highlight-color;\n    a,\n    a:hover {\n      color: @menu-highlight-color;\n    }\n  }\n\n  &:not(&-horizontal) &-item-selected {\n    background-color: @menu-item-active-bg;\n  }\n\n  &-inline,\n  &-vertical,\n  &-vertical-left {\n    border-right: @border-width-base @border-style-base @border-color-split;\n  }\n\n  &-vertical-right {\n    border-left: @border-width-base @border-style-base @border-color-split;\n  }\n\n  &-vertical&-sub,\n  &-vertical-left&-sub,\n  &-vertical-right&-sub {\n    min-width: 160px;\n    max-height: calc(100vh - 100px);\n    padding: 0;\n    overflow: hidden;\n    border-right: 0;\n\n    // https://github.com/ant-design/ant-design/issues/22244\n    // https://github.com/ant-design/ant-design/issues/26812\n    &:not([class*='-active']) {\n      overflow-x: hidden;\n      overflow-y: auto;\n    }\n\n    .@{menu-prefix-cls}-item {\n      left: 0;\n      margin-left: 0;\n      border-right: 0;\n      &::after {\n        border-right: 0;\n      }\n    }\n    > .@{menu-prefix-cls}-item,\n    > .@{menu-prefix-cls}-submenu {\n      transform-origin: 0 0;\n    }\n  }\n\n  &-horizontal&-sub {\n    min-width: 114px; // in case of submenu width is too big: https://codesandbox.io/s/qvpwm6mk66\n  