eight-sm);\n  }\n  &-large {\n    .formSize(@input-height-lg);\n  }\n}\n\n.explainAndExtraDistance(@num) when (@num >= 0) {\n  padding-top: floor(@num);\n}\n\n.explainAndExtraDistance(@num) when (@num < 0) {\n  margin-top: ceil(@num);\n  margin-bottom: ceil(@num);\n}\n\n// ================================================================\n// =                             Item                             =\n// ================================================================\n.@{form-item-prefix-cls} {\n  .reset-component();\n\n  margin-bottom: @form-item-margin-bottom;\n  vertical-align: top;\n\n  &-with-help {\n    margin-bottom: 0;\n  }\n\n  &-hidden,\n  &-hidden.@{ant-prefix}-row {\n    // https://github.com/ant-design/ant-design/issues/26141\n    display: none;\n  }\n\n  // ==============================================================\n  // =                            Label                           =\n  // ==============================================================\n  &-label {\n    display: inline-block;\n    flex-grow: 0;\n    overflow: hidden;\n    white-space: nowrap;\n    text-align: right;\n    vertical-align: middle;\n\n    &-left {\n      text-align: left;\n    }\n\n    > label {\n      position: relative;\n      // display: inline;\n      display: inline-flex;\n      align-items: center;\n      height: @form-item-label-height;\n      color: @label-color;\n      font-size: @form-item-label-font-size;\n\n      > .@{iconfont-css-prefix} {\n        font-size: @form-item-label-font-size;\n        vertical-align: top;\n      }\n\n      // Required mark\n      &.@{form-item-prefix-cls}-required:not(.@{form-item-prefix-cls}-required-mark-optional)::before {\n        display: inline-block;\n        margin-right: 4px;\n        color: @label-required-color;\n        font-size: @form-item-label-font-size;\n        font-family: SimSun, sans-serif;\n        line-height: 1;\n        content: '*';\n\n        .@{form-prefix-cls}-hide-required-mark & {\n          dis